import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,Events } from 'ionic-angular';
import { HttpServicesProvider } from '../../../../providers/http-services';
import { WorkflowServiceProvider } from '../../../../providers/workflow-service';
import { ToastController } from 'ionic-angular';
/**
 * Generated class for the StartPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-workflowstart',
  templateUrl: 'workflowstart.html',
})
export class WorkflowstartPage {
  public workflowinfo={};
  public bussid:string;
  public busstype:string;
  public memo = '';
  public falg = false;
  constructor(public navCtrl: NavController, public navParams: NavParams,
              public toastCtrl: ToastController,
              public httpService:HttpServicesProvider,
              public workflow:WorkflowServiceProvider,public events: Events) {
  }
  ionViewWillEnter(){
    this.bussid=this.navParams.get("bussid");
    this.busstype=this.navParams.get("busstype");
    this.workflowinfo['memo']='';
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad StartPage');
  }
  startAndSenf(){
    this.falg = true;
    this.workflowinfo['memo']=this.memo;
    var url='/workflow/startAndSenf/'+this.busstype+'/'+this.bussid;
    this.httpService.postform(url,null,this.workflowinfo,(result)=>{
      let toast = null;
      if(result.code==0){
        console.log('发送成功');
        toast = this.toastCtrl.create({
          message: result.message,
          duration: 1000,
          position: 'middle'
        })
      }else{
        console.log('发送失败');
        toast = this.toastCtrl.create({
          message: result.message,
          duration: 1000,
          position: 'middle'
        })
      }
      toast.present();
      this.navCtrl.pop();
      this.events.publish('workflowPage',true,Date.now());

      // 
    })
  }
}



